<template>
	<div style="background: rgba(242, 242, 242, 1)">

		<!-- 内容 -->
		<div class="lf-store-name" >
      <div class="lf-content-payPanel">
        <div>
          <div class="one">
            <span class="">付款给&nbsp;<span v-text="this.merchantName"></span></span>
            <div><img :src="headSculpture" class="headSculpture" alt=""></div>
          </div>
        </div>
      </div>

    </div>
     <div class="bottom" style="height: 37rem;">
       <!-- 金额 -->
       <div class="jiner">
         <div class="text">金额</div>
         <div class="money">
           <span class="money_fuhao">￥</span>
           <span v-if="isAllowModifyAmount" class="text-lg pay-price-input" v-text="keyboardOption.value" @click="showKeyboard"
             ref="keyboard"></span>
           <span v-else class="text-lg pay-price-input" v-text="keyboardOption.value"></span>
           <Keyboard :keyboardOption="keyboardOption" :toPayFunc="toPayFunc"></Keyboard>
         </div>
       </div>
       <div class="fukuan" v-if="keyboardOption.show==false">
         <div  class="fukuan_text" @click="payment"><span class="zhifu-text1">付款{{keyboardOption.value}}元</span></div>
       </div>
     </div>
     <!-- 1 -->

<!-- 		<div class="lf-pay-panel" style="height: 12rem;">
			<div>
        <div>
          <span class="">向 [<span style="font-style: italic;" v-text="this.merchantName"></span>] 付款：</span>
        </div>
				<div class="one">
					<span class="text-sm">请向收银员询问应付金额</span>
				</div>
				<div class="two">
          <span class="text-llg">{{yenSymbol}}</span>
          <span v-if="isAllowModifyAmount" class="text-lg pay-price-input" v-text="keyboardOption.value" @click="showKeyboard" ref="keyboard"></span>
          <span v-else class="text-lg pay-price-input" v-text="keyboardOption.value"></span>
          <Keyboard :keyboardOption="keyboardOption" ></Keyboard>
				</div>
				<div class="three">
					<input v-model="remark" class="text-grey" type="text" maxlength="20" placeholder="添加备注(20个字以内)">
				</div>
			</div>
		</div>
		<div class="lf-pay-btn" :style="{height: htmlProportion * 200 + 'px'}">
			<div @click="payment">
				<span class="text-lg text-fs">付 款</span>
			</div>
		</div>
    <div class="zfb-mask" v-show="showMask"></div> -->
	</div>
</template>

<script>
	import {getMchInfo, getAlipayPay} from '../../../api/vueAPI.js'
  import Keyboard from '../../../components/keyboard'

	export default{
    components: {Keyboard},
    data() {
			return{

				htmlProportion: '',		//屏幕长宽比例
				htmlWidth: '',		//屏幕宽
				htmlheight: '',		//屏幕高
				yenSymbol: '\u00A5',	//金钱符号

        isAllowModifyAmount : true, //是否允许修改金额

        //自定义软键盘 数据对象
        keyboardOption: {
          show: false,   //是否显示
          clickShowDom: "",   //仅当前为该对象时显示，否则会自动隐藏， 一般为显示金额的dom
          value: ""   //金额
        },

        payToken: "", //token
        storeId: "",  //门店ID
        operatorId: "",  //操作员ID
        tradeOrderId: "", //tradeOrderId

				merchantName: ' -- ',
        showMask: false,
        remark: '' ,// 备注
        headSculpture: require("../../../assets/MoRenTouXiang.png")
			}
		},
		mounted(){
		  this.judge_shuru();
		  },
		created() {

			this.htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			this.htmlheight = document.documentElement.clientHeight || document.body.clientHeight;
			this.htmlProportion = this.htmlWidth / 750 ;


      //2. 获取url参数
      //alipayUserId(必填)
      let alipayUserId = this.$route.query.alipayUserId;
      if(!alipayUserId) return alert('参数有误');
      this.alipayUserId = alipayUserId;

      //获取mchId信息(必填)
      let mchId = this.$route.query.mchId;
      if(!mchId) return alert('参数有误');
      this.mchId = mchId;

      //店铺ID（非必填）
      let storeId = this.$route.query.storeId;
      if(storeId){
        this.storeId = storeId
      }

      //操作员ID（非必填）
      let operatorId = this.$route.query.operatorId;
      if(operatorId){
        this.operatorId = operatorId;
      }
      //金额（非必填） 单位： 分
      let payAmount = this.$route.query.payAmount;
      if(payAmount){ //默认金额， 不支持修改
        this.keyboardOption.value = payAmount / 100;
      }else{
        this.isAllowModifyAmount = true;  //支持修改
      }

      //tradeOrderId
      let tradeOrderId = this.$route.query.tradeOrderId;
      if(tradeOrderId){
        this.tradeOrderId = tradeOrderId;
      }

      //获取payToken信息(必填)
      let payToken = this.$route.query.payToken;
      if(!payToken) return alert('参数有误');
      this.payToken = payToken;

      //异步获取商户名称
      getMchInfo(this.payToken, this.mchId, this.storeId, this.operatorId).then(res => {
        this.merchantName = res.data.mchName;
      }).catch(error => {});

		},
		methods:{
      showKeyboard(){
        this.keyboardOption.clickShowDom = this.$refs['keyboard'];  //$ref仅页面加载完成后有效， 放置需要显示的input
        this.keyboardOption.show = true;
      },
	  // 判断是否能输入而自动弹出键盘
	  judge_shuru() {
	    if (this.isAllowModifyAmount) {
	      this.showKeyboard();
	      this.money_shuru = true;
	    }
	  },

			/* 付款 */
			payment() {

        this.showMask = true; //开启遮罩层
        let that = this;
        //获取支付宝支付参数

        if(!this.keyboardOption.value){
          this.showMask = false;
          return alert('请输入正确的金额！');
        }

        let payAmount = (this.keyboardOption.value * 100 ).toFixed(0); //支付金额：元 -> 分
        if(payAmount <= 0){
          this.showMask = false;
          return alert('请输入正确的金额！');
        }

        getAlipayPay(this.alipayUserId, payAmount, this.storeId, this.operatorId, this.mchId, this.tradeOrderId, this.payToken).then(res => {

          let payParam = JSON.parse(res.data.payParam);
          let alipayTradeNo = payParam.alipayTradeNo;  //支付宝交易单号

          if (!window.AlipayJSBridge) {
            document.addEventListener('AlipayJSBridgeReady', function(){
              that.doAlipay(alipayTradeNo, res.data);
            }, false);
          }else{
            that.doAlipay(alipayTradeNo, res.data);
          }

        }).catch(err => {
          this.showMask = false; //关闭遮罩层
        });
			},
      doAlipay(alipayTradeNo, resData){

			  let that = this;
        AlipayJSBridge.call("tradePay", {
          tradeNO: alipayTradeNo
        }, function (data) {
          if ("9000" == data.resultCode) {
            that.$router.push({
              path: "/paySuccess",
              query:{
                orderAmount: resData.orderAmount,
                discountAmount: resData.discountAmount,
                amount: resData.amount,
                tradeOrderId: resData.tradeOrderId,
                payType: 2
              }
            })
          }else{ //其他
            that.showMask = false;
          }
        });
      },

      //点击 【确认】 回调事件
      toPayFunc() {

        this.$nextTick(() => {
          this.payment();
        });
      }

		}
	}
</script>

<style scoped>
@import url("../../wx/paySelect/index.css");
.fukuan{
  box-shadow: 0 2px 8px #108ee9;
  background: #108ee9;
  -webkit-box-shadow: 0 2px 8px #108ee9;
  margin-top: 110%;
}

</style>
